@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.App {
    width: 100vw;
    min-height: 100vh;
    background: #4a536b;
}

.logo {
    color: #ffea04;
    margin: 30px;
    font-family: 'Poppins', sans-serif;
    width: -webkit-max-content;
    width: max-content;
}

.viewPort {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 100vh;
}

.CodeMirror {
    min-height: 40px;
}

.CodeMirror-lines {
    margin-top: 0px;
    margin-bottom: 10px;
}

.CodeMirror-code > div {
    padding-top: 5px;
}

.CodeMirror-cursor, .CodeMirror-selected {
    margin-top: 5px;
}


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.code-outer-container {
    background: #aee6dc;
    padding: 50px;
    min-width: 213px;
    max-width: 80%;
}

.code-container {
    background: #151718;
    border-radius: 5px;
    box-shadow: 0px 10px 15px rgb(0,0,0,0.3);
}

.top-bar {
    background: #151718;
    border-radius: 5px;
}

.dot-container {
    padding: 8px;
}

.dot {
    display: inline-block;
    height: 12px;
    width: 12px;
    border-radius: 50%;
    margin: 3px;
    vertical-align: middle;
}

.dot-red {
    background: #FF605C;
}

.dot-yellow {
    background: #FFBD44;
}

.dot-green {
    background: #00CA4E;
}
button {
    margin: 20px 0px;
    height: 40px;
    width: 200px;
    background: #ffea04;
    color: #4a536b;
    font-size: 1em;
    border-radius: 20px;
    outline: none;
    border: none;
    box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
    cursor: pointer;
}

button:active {
    height: 38px;
    width: 198px;
    border: 2px solid coral;
}
.settings-container {
    margin-bottom: 20px;
    border: 2px solid #ffea04;
    border-radius: 5px;
    padding: 5px;
    display: flex;
}

select {
    background: none;
    border: none;
    outline: none;
    color: white;
}
